.menu-aside {
  list-style: none;
  margin: 1rem;
  padding: 0;
}
.menu-aside a {
  display: block;
  text-decoration: none;
  font-family: "Quicksand", sans-serif;
  font-weight: 600;
}
.menu-aside .menu-item {
  margin-bottom: 5px;
}
.menu-aside .menu-item .icon {
  color: #adb5bd;
  margin-right: 10px;
  font-size: 24px;
}
.menu-aside .menu-item.active .icon {
  color: #3BB77E;
}
.menu-aside .menu-item.active .menu-link {
  background-color: rgba(59, 183, 126, 0.2);
}
.menu-aside .menu-item.active .submenu a.active {
  color: #3BB77E !important;
}
.menu-aside .menu-link {
  padding: 10px;
  font-weight: 800;
  font-size: 13px;
  color: #292f46;
  border-radius: 0.5rem;
  position: relative;
  line-height: 1;
  display: flex;
  align-items: center;
}
.menu-aside .menu-link:hover {
  transition: 0.2s linear;
  background-color: #e9ecef;
}
.menu-aside .submenu {
  margin-left: 44px;
  display: none;
  margin-top: 10px;
}
.menu-aside .submenu a {
  color: #6c757d;
  padding: 5px 0 5px 15px;
  transition-duration: 0.3s;
  position: relative;
  margin-left: 5px;
}
.menu-aside .submenu a:hover {
  color: #000;
  transition-duration: 0.3s;
}
.menu-aside .submenu a:before {
  content: "";
  width: 5px;
  height: 5px;
  position: absolute;
  background-color: #adb5bd;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
}
.menu-aside .menu-item.active .submenu {
  display: block;
}
.menu-aside .menu-item.has-submenu > .menu-link:after {
  display: inline-block;
  position: absolute;
  right: 10px;
  top: 15px;
  margin-top: 0.6em;
  vertical-align: middle;
  content: "";
  border-top: 5px solid #adb5bd;
  border-right: 5px solid transparent;
  border-bottom: 0;
  border-left: 5px solid transparent;
}

.nav-pills .nav-link {
  color: #6c757d;
  font-weight: 500;
}
.nav-pills .nav-link:hover {
  color: #1e2231;
  background-color: rgba(173, 181, 189, 0.15);
}

/*Pagination*/
.pagination-area .page-item {
  margin: 0 5px;
}
.pagination-area .page-item:first-child {
  margin-left: 0;
}
.pagination-area .page-item:first-child .page-link {
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}
.pagination-area .page-item:last-child .page-link {
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}
.pagination-area .page-item.active .page-link, .pagination-area .page-item:hover .page-link {
  color: #fff;
  background: #3BB77E;
}
.pagination-area .page-item .page-link {
  border: 0;
  padding: 0 10px;
  box-shadow: none;
