body {
  position: relative;
  min-height: 100vh;
  overflow-x: hidden;
}

body.offcanvas-active {
  overflow: hidden;
}

.main-wrap {
  margin-left: 300px;
  background-color: #f8f9fa;
  position: relative;
}
.main-wrap .content-main {
  min-height: calc(100vh - 60px);
}

.content-main {
  padding: 30px 3%;
  margin-left: auto;
  margin-right: auto;
  background-color: #f8f9fa;
  min-height: calc(100vh - 60px);
}

.dropdown-menu-end {
  right: 0 !important;
}

.navbar-aside {
  max-width: 300px;
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  overflow-y: auto;
  background-color: #fff;
  box-shadow: none;
  z-index: 10;
  border-right: 1px solid #dee2e6;
}
.navbar-aside .aside-top {
  padding: 12px 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
  min-height: 72px;
}
.navbar-aside .aside-top .brand-wrap {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  display: inline-block;
}
.navbar-aside .aside-top .logo {
  max-width: 120px;
  margin-top: 5px;
  min-width: 120px;
}

.main-header {
  padding-left: 3%;
  padding-right: 3%;
  min-height: 72px;
  background-color: #fff;
  box-shadow: 0 0.75rem 1.5rem rgba(18, 38, 63, 0.03);
  border-bottom: 1px solid #dee2e6;
}
.main-header .nav {
  align-items: center;
}
.main-header .nav-item > a {
  color: #6c757d;
  display: block;
  text-decoration: none;
  position: relative;
  padding: 0 10px;
}
.main-header .nav-item > a .badge {
  position: absolute;
  right: 2px;
  top: -5px;
  color: #ffffff;
  height: 16px;
  width: 16px;
  font-weight: 500;
  font-size: 10px;
  text-align: center;
  line-height: 17px;
  display: block;
  padding: 0;
  background-color: #3BB77E;
}
.main-header .nav-item.dropdown {
  margin-right: 5px;
}
.main-header .nav-link:hover {
  background-color: transparent;
  color: #3BB77E;
}
.main-header .col-search {
  flex-grow: 0.5;
}
.main-header .col-nav {
  display: flex;
  align-items: center;
}
.main-header .brand-wrap img.logo {
  max-width: 130px;
  margin-top: 5px;
}

.nav-item img.rounded-circle {
  border: 2px solid #eee;
}
.nav-item img.rounded-circle:hover {
  border-color: #3BB77E;
}

.content-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}
.content-header .content-title {
  margin-bottom: 0;
}

.inner {
  padding: 1rem;
}

body.aside-mini .main-wrap {
  margin-left: 80px;
}
body.aside-mini .navbar-aside {
  max-width: 80px;
  overflow: visible;
  position: absolute;
  min-height: 100%;
